import React, { Component } from 'react';
import styles from './MainBody.module.css';
import downArrowPng from './assets/down-arrow.png';
import notificationPng from './assets/notification.svg';
import messagePng from './assets/message.svg';
import profilePicPng from './assets/profile-pic.png';
import cancelPng from './assets/cancel-mobile.png';
import profilePng from './assets/user.png';
import updatePng from './assets/update.png';
import exitPng from './assets/exit.png';
import saveMoneyPng from './assets/save-money.svg';
import logoPng from './assets/logo-mobile.svg';
import messageMobilePng from './assets/message-mobile.svg';
import notificationMobilePng from './assets/notification-mobile.svg';
import menuMobile from './assets/menu-mobile.svg';
import iPng from './assets/i.svg';
import barCodePng from './assets/bar-code.svg';
import qrPng from './assets/qr-code.png';
import covidPng from './assets/covid.png';
import missionPng from './assets/mission.png';
import home1Png from './assets/home1.png';
import home2Png from './assets/home2.png';
import rejolutPng from './assets/rejolut.png';

let ismobileDevice = false;
if ( typeof window.orientation !== 'undefined' ) {
      ismobileDevice = true;
}

class MainBody extends Component {
    
    render() {
        let initialProfileClass = ismobileDevice ? '' : 'none';
        let TransactionCards = (
            <div>
                <TransactionCard date={'09 March 2020'}
                                transaction={'20922 Avalon Drive (Jon hea...)'}
                                ttype={'Dividend'}
                                amount={'$10.00'}
                                cls={styles.green} />
                <TransactionCard date={'09 March 2020'}
                                transaction={'20922 Avalon Drive (Jon hea...)'}
                                ttype={'Purchase'}
                                amount={'-$1000.00'} />
                <TransactionCard date={'09 March 2020'}
                                transaction={'Freedom Coin'}
                                ttype={'Deposit'}
                                amount={'$10.00'}
                                cls={styles.green} />
                <TransactionCard date={'09 March 2020'}
                                transaction={'Freedom Coin'}
                                ttype={'Withdrawal'}
                                amount={'$500.00'} />
                <TransactionCard date={'09 March 2020'}
                                transaction={'Profit wolfpack'}
                                ttype={'Purchase'}
                                amount={'$400.00'} />
                <TransactionCard date={'09 March 2020'}
                                transaction={'Profit wolfpack'}
                                ttype={'Contribute'}
                                amount={'$400.00'} />
            </div>
        );
        return (
            <div className={styles.mainBody}>
                {
                    !ismobileDevice && (
                        <div className={styles.upperBar}>
                            <div className={styles.upperBarInner1}>
                                <p className={styles.selectedItem}>Overview</p>
                                <p>Savings</p>
                                <p>Investments</p>
                            </div>
                            <div className={styles.upperBarInner2}>
                                <img className={'pointer'} src={notificationPng} alt={'Pic'}></img>
                                <img className={'pointer'} src={messagePng} alt={'Pic'}></img>
                                <div className={`${styles.investor} pointer`}>
                                    <div>
                                        <img src={saveMoneyPng} alt={'Pic'}></img>
                                    </div>
                                    <p>Investor</p>
                                </div>

                                <div className={styles.verticalLine}></div>

                                <img src={profilePicPng} style={{width: '25px'}} alt={'Pic'}></img>
                                <h5>Robert Downey JR</h5>
                                <div className={styles.profileLink}>
                                    <img id={'downArrow'} className={`pointer`} src={downArrowPng} alt={'Pic'} onClick={this.showProfile}></img>
                                </div>
                            </div>
                        </div>
                    )
                }

                <div id={'profile'} className={`${styles.profileDiv} ${initialProfileClass}`}>
                    {
                        ismobileDevice && (
                            <div style={{flexDirection: 'row-reverse'}}>
                                <img src={cancelPng} alt={'Pic'} onClick={this.showProfile}></img>
                            </div>
                        )
                    }
                    <div>
                        <img src={profilePng} alt={'Pic'}></img>
                        <p>Profile</p>
                    </div>
                    {
                        ismobileDevice && (<hr />)
                    }
                    <div>
                        <img src={updatePng} alt={'Pic'}></img>
                        <p>Update Account</p>
                    </div>
                    {
                        ismobileDevice && (<hr />)
                    }
                    <div>
                        <img src={exitPng} alt={'Pic'}></img>
                        <p>Log Out</p>
                    </div>
                </div>

                {
                    ismobileDevice && (
                        <div className={styles.upperBarMobile}>
                            <div className={`dflex ${styles.upperBarMobile1}`}>
                                <div>
                                    <img src={logoPng} alt={'Pic'}></img>
                                </div>
                                <div className={`dflex ${styles.upperBarMobileInner2}`}>
                                    <img src={messageMobilePng} alt={'Pic'}></img>
                                    <img src={notificationMobilePng} alt={'Pic'}></img>
                                    <img src={profilePicPng} style={{width: '25px'}} alt={'Pic'} onClick={this.showProfile}></img>
                                </div>
                            </div>

                            <div className={`dflex ${styles.upperBarMobile2}`}>
                                <div className={`dflex ${styles.upperBarMobileInner3}`}>
                                    <img src={menuMobile} style={{marginRight: "15px"}} onClick={this.showMenuBar} alt={'Pic'}></img>
                                    <h5>Wallet</h5>
                                </div>
                                <div>
                                    <div className={styles.investor}>
                                        <div>
                                            <img src={saveMoneyPng} alt={'Pic'}></img>
                                        </div>
                                        <p>Investor</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }

                <div className={styles.body}>
                    <div className={styles.bodyInner1}>

                        <div className={`dflex ${styles.cardDiv}`} style={{marginTop: '15px'}}>
                            <div style={{display: 'flex', alignItems: 'center'}}>
                                <p>Total Balance</p>
                                &nbsp;
                                <img src={iPng} style={{width: '15px'}} alt={'Pic'}></img>
                                &nbsp;
                                <p> : $60.00</p>
                            </div>
                            <div style={{display: 'flex', alignItems: 'center'}}>
                                <p>Wallet Address</p>
                                &nbsp;
                                <img src={barCodePng} className={'pointer'} style={{width: '20px'}} alt={'Pic'} onClick={this.showQrCode}></img>
                                <div id={'barCode'} className={`${styles.barCodeDiv} none`}>
                                    <img src={qrPng} alt={'Pic'}></img>
                                </div>
                            </div>
                        </div>
                        <div className={styles.cardDiv}>
                            <div className={styles.card}>
                                <p className={styles.cardTitle}>Checking</p>
                                <div className={styles.cardBody}>
                                    <p>Cash : <b>$20.00</b></p>
                                    <p>Buy coins<sup>TM</sup></p>
                                    <div>
                                        <input className={styles.numberInput} type={'number'} defaultValue={0} placeholder={0}></input>
                                        <button title={'Read FAQ'}>Buy</button>
                                    </div>
                                </div>
                            </div>
                            <div className={styles.card}>
                                <p className={styles.cardTitle}>Transfers</p>
                                <div className={styles.cardBody}>
                                    <p>Paypal : <b>Bank Account</b></p>
                                    <p>Sell coins<sup>TM</sup></p>
                                    <div>
                                        <input className={styles.numberInput} type={'number'} defaultValue={0} placeholder={0}></input>
                                        <button title={'Read FAQ'}>Sell</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className={styles.cardDiv}>
                            <div className={styles.card}>
                                <p className={styles.cardTitle}>Savings</p>
                                <div className={styles.cardBody}>
                                    <p>Invested : <b>$20.00</b></p>
                                    <p>Total Payout : <b>$20.00</b></p>
                                </div>
                            </div>
                            <div className={styles.card}>
                                <p className={styles.cardTitle}>Investments</p>
                                <div className={styles.cardBody}>
                                    <p>Invested : <b>$20.00</b></p>
                                    <p>Contributed : <b>$20.00</b></p>
                                </div>
                            </div>
                        </div>

                        <div style={{marginTop: '25px'}}>
                            <div>
                                {
                                    !ismobileDevice && (
                                        <div>
                                            <h5>Recent Transactions</h5>
                                            <div className={styles.transactionsHeader} style={{margin: '15px 0px'}}>
                                                <div className={styles.heading1}>
                                                    <h5>Date</h5>
                                                </div>
                                                <div className={styles.heading2}>
                                                    <h5>Transaction</h5>
                                                </div>
                                                <div className={styles.heading1}>
                                                    <h5>Amount</h5>
                                                </div>
                                            </div>
                                            <hr />
                                        </div>
                                    )
                                }

                                {
                                    !ismobileDevice && (TransactionCards)
                                }

                                {
                                    !ismobileDevice && (
                                        <div className={styles.end} style={{textAlign: 'right'}}>
                                            <h5>View More</h5>
                                        </div>
                                    )
                                }
                            </div>
                        </div>
                    </div>

                    <div className={styles.bodyInner2}>
                        <div>
                            {
                                ismobileDevice ? (
                                    <h5 style={{marginBottom: '10px', textAlign: 'center'}}>Available Assets</h5>
                                ) : (
                                    <h5 style={{marginBottom: '10px'}}>Available Assets</h5>
                                )
                            }
                            {
                                !ismobileDevice && (<hr />)
                            }
                        </div>
                        <div>
                            <Asset pic={covidPng}
                                    title={'Potential COVID-19 cure: Clinical trail'}
                                    desc={'A well known safe generic medication that has been used for shown too...'} />
                            <Asset pic={missionPng}
                                    title={'Mission'}
                                    desc={'Owenership in minuteman is decentralized and distributed across its base of...'} />
                            <Asset pic={home1Png}
                                    title={'20922 Avalon Drive'}
                                    desc={'8801 Holy road, Tullahoma TN37388'} />
                            <Asset pic={home2Png}
                                    title={'17456 Eastage Drive'}
                                    desc={'23 North Briarwood Avenue, Willimsburg, VA 23185'} />
                        </div>

                        {
                            ismobileDevice ? (
                                <div className={styles.end} style={{textAlign: 'center'}}>
                                    <h5>View More</h5>
                                </div>
                            ) : (
                                <div className={styles.end} style={{textAlign: 'right'}}>
                                    <h5>View More</h5>
                                </div>
                            )
                        }
                    </div>

                    {
                        ismobileDevice && (
                            <h5 style={{margin: '20px 0px 15px 0px', textAlign: 'center'}}>Recent Transactions</h5>
                        )
                    }

                    {
                        ismobileDevice && (TransactionCards)
                    }
                </div>

                <div style={{display: 'flex', justifyContent: 'flex-end', margin: '-5px 5px 10px 0'}}>
                    <img src={rejolutPng} style={{width: '45px'}} alt={'Pic'}></img>
                </div>
            </div>
        );
    }

    componentDidMount = () => {
        this.menuBar = document.getElementById('menuBar');
        this.downArrow = document.getElementById('downArrow');
        this.profile = document.getElementById('profile');
        this.barCode = document.getElementById('barCode');

        this.profileDisplayed = false;
    }

    showMenuBar = () => {
        this.menuBar.style.left = '0';
    }

    showProfile = () => {
        if (ismobileDevice) {
            if (this.profileDisplayed) {
                this.profile.style.right = '-100%';
            }
            else {
                this.profile.style.right = '0';
            }
        }
        else {
            if (this.profileDisplayed) {
                this.downArrow.style.transform = 'rotate(0deg)';
            }
            else {
                this.downArrow.style.transform = 'rotate(180deg)';
            }
            this.profile.classList.toggle('none');
        }
        this.profileDisplayed = !this.profileDisplayed;
    }

    showQrCode = () => {
        this.barCode.classList.toggle('none');
    }
}

class TransactionCard extends Component {

    render() {
        let cardClass = ismobileDevice ? styles.cardTransaction : '';
        let sep = ismobileDevice ? ':' : '';
        return (
            <div className={`${styles.transactionsBody} ${cardClass}`}>
                <div className={styles.transactionsBodyText1}>
                    {
                        ismobileDevice && <p className={styles.transactionHeading}>Date</p>
                    }
                    <p>{`${sep}  ${this.props.date}`}</p>
                </div>
                <div className={styles.transactionsBodyText2}>
                    {
                        ismobileDevice && <p className={styles.transactionHeading}>From</p>
                    }
                    <p>{`${sep}  ${this.props.transaction}`} - <b><i>{this.props.ttype}</i></b></p>
                </div>
                <div className={styles.transactionsBodyText1}>
                    {
                        ismobileDevice && <p className={styles.transactionHeading}>Coins</p>
                    }
                    <p className={this.props.cls}>{`${sep}  ${this.props.amount}`}</p>
                </div>
            </div>
        );
    }
}

class Asset extends Component {
    render() {
        return (
            <div>
                <div className={styles.asset}>
                    <img src={this.props.pic} alt={'Pic'}></img>
                    <div>
                        <h5>{this.props.title}</h5>
                        {
                            ismobileDevice && (<div style={{margin: '8px'}}></div>)
                        }
                        <p style={{marginTop: '5px', fontWeight: 'bold', color: '#616161'}}>Description:</p>
                        <p>{this.props.desc}</p>
                    </div>
                </div>
                <hr />
            </div>
        );
    }
}

export default MainBody;