import React, { Component } from 'react';
import styles from './SideBar.module.css';
import logoPng from './assets/logo.svg';
import cancelPng from './assets/cancel.svg';
import walletPng from './assets/wallet.svg';
import wolfPacksPng from './assets/wolf-packs.svg';
import portfolioPng from './assets/portfolio.svg';
import searchPng from './assets/search.svg';
import propertiesPng from './assets/properties.svg';
import rightPng from './assets/right.svg';

let ismobileDevice = false;
if ( typeof window.orientation !== 'undefined' ) {
      ismobileDevice = true;
}

class SideBar extends Component {

    render() {
        return (
            <div id={'menuBar'} className={styles.sideBar}>
                <div>
                    <div className={styles.logo}>
                        <img src={logoPng} alt={'Pic'}></img>
                        {
                            !ismobileDevice && <hr />
                        }
                    </div>
                    {
                        ismobileDevice && (
                            <div onClick={this.closeMenuBar}>
                                <img src={cancelPng} alt={'Pic'}></img>
                            </div>
                        )
                    }
                    <div className={styles.menu}>
                        <div className={styles.selectedItem}>
                            <img src={walletPng} alt={'Pic'}></img>
                            <p>Wallet</p>
                        </div>
                        <div>
                            <img src={wolfPacksPng} alt={'Pic'}></img>
                            <p>Wolfpackes</p>
                        </div>
                        <div>
                            <img src={portfolioPng} alt={'Pic'}></img>
                            <p>Portfolio</p>
                        </div>
                        <div>
                            <img src={searchPng} alt={'Pic'}></img>
                            <p>Transparency</p>
                        </div>
                        <div>
                            <img src={propertiesPng} alt={'Pic'}></img>
                            <p>All Properties</p>
                        </div>
                    </div>
                </div>

                {
                    !ismobileDevice && (
                        <div id={'messageDiv'} className={styles.bottomMessage}>
                            <img className={styles.right} src={rightPng} alt={'Pic'}></img>
                            <img className={styles.cancel} src={cancelPng} alt={'Pic'} onClick={this.closeMessageDiv}></img>
                            <p style={{marginTop: '30px'}}>
                                Your account is not yet activated to self freedom Coins
                                <sup>TM</sup>
                                . Please specify your Paypal payment address to activate your account
                            </p>
                            <button style={{marginTop: '15px', width: '100%', backgroundColor: '#FFB400'}}>Click here</button>
                        </div>
                    )
                }
            </div>
        );
    }

    componentDidMount = () => {
        this.menuBar = document.getElementById('menuBar');
        this.messageDiv = document.getElementById('messageDiv');
    }

    closeMenuBar = () => {
        this.menuBar.style.left = '-100%';
    }

    closeMessageDiv = () => {
        this.messageDiv.style.bottom = '-180px';
    }
}

export default SideBar;